<template>
  <div class="bankProduct-box">
    <el-row>
      <el-col :span="4">
        <el-select v-model="param.userName" clearable placeholder="">
          <el-option v-for="item in userNames" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-select v-model="param.bankName" clearable placeholder="">
          <el-option v-for="item in bankNames" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-col>
      <el-col :span="4" :offset="1">
        <el-button type="primary" icon="el-icon-search" @click="getMyBankProducts"></el-button>
      </el-col>
      <el-col :span="2" :offset="9">
        <el-button type="primary" icon="el-icon-circle-plus-outline" @click="handleAdd"></el-button>
        <!-- <el-button type="danger" icon="el-icon-delete" size="small" @click="mulDelete"></el-button> -->
      </el-col>
    </el-row>

    <el-table
      :data="bankMyProducts"
      show-summary
      stripe
      @selection-change="selectChange"
      style="width: 100%"
    >
      <el-table-column type="selection"></el-table-column>

      <el-table-column prop="bank.name" label=""></el-table-column>
      <el-table-column prop="bank.bankName" label=""></el-table-column>
      <el-table-column prop="bank.bankCard" label="" label-width="200px"></el-table-column>
      <el-table-column prop="productType" label=""></el-table-column>
      <el-table-column prop="investmentAmount" :formatter="formatter" width="150px" label=""></el-table-column>

      <el-table-column prop="buyingTime" label="" width="100px"></el-table-column>
      <el-table-column prop="dueTime" label="" width="100px"></el-table-column>
      <el-table-column prop="interestStartTime" width="100px" label=""></el-table-column>
      <el-table-column prop="expectedInterestRate" label=""></el-table-column>
      <el-table-column prop="interestRate" label=""></el-table-column>

      <el-table-column prop="interestPaymentMethod" label=""></el-table-column>
      <el-table-column prop="profitDate" label="" width="100px"></el-table-column>
      <el-table-column
        prop="expectedInterestIncomeMonth"
        width="150px"
        :formatter="formatter"
        label=""
      ></el-table-column>
      <el-table-column
        prop="expectedInterestIncomeTotal"
        width="150px"
        :formatter="formatter"
        label=""
      ></el-table-column>
      <el-table-column
        prop="totalEffectiveInterestIncome"
        width="150px"
        :formatter="formatter"
        label=""
      ></el-table-column>

      <el-table-column prop="principalAndInterestIncome" :formatter="formatter" label=""></el-table-column>
      <el-table-column prop="down" label=""></el-table-column>
      <el-table-column prop="dueTime" label=""></el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
      <el-table-column label="" fixed="right" width="200">
        <template slot-scope="scope">
          <el-row :gutter="20" v-if="scope.row.state == 1">
            <el-col :span="12">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="handleEdit(scope.$index, scope.row)"
              ></el-button>
            </el-col>
            <el-col :span="12">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="redeem(scope.$index, scope.row)"
              ></el-button>
            </el-col>
          </el-row>
          <el-row :gutter="20" v-if="scope.row.state == 1">
            <el-col :span="12">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="income(scope.$index, scope.row)"
              ></el-button>
            </el-col>
            <el-col :span="12">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="reminderRule(scope.$index, scope.row)"
              ></el-button>
            </el-col>
          </el-row>

          <el-row :gutter="20" v-if="scope.row.state == 2">
            <el-col :span="24"></el-col>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      :page-sizes="[10, 20, 30, 50]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="400"
    ></el-pagination>

    <el-dialog
      :title="dialogTitle"
      width="700px"
      :visible.sync="buyBankProductFormVisible"
      @close="resetForm('buyMyProductForm')"
    >
      <el-form :model="buyMyProduct" :rules="buyMyProductRules" ref="buyMyProductForm">
        <el-form-item label=":" prop="bankCardId" label-width="100px">
          <el-select v-model="buyMyProduct.bankCardId" filterable clearable placeholder="">
            <el-option
              v-for="item in banks"
              :key="item.id"
              :label="item.selectName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="productType" label-width="100px">
          <el-input v-model="buyMyProduct.productType" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="investmentAmount" label-width="100px">
          <font size="4" face="arial">{{this.Utils.transform(buyMyProduct.investmentAmount) }}</font>
          <el-input v-model="buyMyProduct.investmentAmount" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="buyingTime" label-width="100px">
          <el-date-picker v-model="buyMyProduct.buyingTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label=":" value-format="yyyy-MM-dd" prop="dueTime" label-width="100px">
          <el-date-picker v-model="buyMyProduct.dueTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item
          label=":"
          value-format="yyyy-MM-dd"
          prop="interestStartTime"
          label-width="100px"
        >
          <el-date-picker v-model="buyMyProduct.interestStartTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="expectedInterestRate" label-width="100px">
          <el-input v-model="buyMyProduct.expectedInterestRate" placeholder=",%"></el-input>
        </el-form-item>

        <el-form-item label=":" prop="interestPaymentMethod" label-width="100px">
          <el-input v-model="buyMyProduct.interestPaymentMethod" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" value-format="yyyy-MM-dd" prop="profitDate" label-width="100px">
          <el-date-picker v-model="buyMyProduct.profitDate" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="expectedInterestIncomeMonth" label-width="100px">
          <font
            size="4"
            face="arial"
          >{{this.Utils.transform(buyMyProduct.expectedInterestIncomeMonth) }}</font>
          <el-input v-model="buyMyProduct.expectedInterestIncomeMonth" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="expectedInterestIncomeTotal" label-width="100px">
          <font
            size="4"
            face="arial"
          >{{this.Utils.transform(buyMyProduct.expectedInterestIncomeTotal) }}</font>
          <el-input v-model="buyMyProduct.expectedInterestIncomeTotal" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label=":" prop="remark" label-width="100px">
          <el-input v-model="buyMyProduct.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="buyBankProductFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitMyBankProduct('buyMyProductForm')"> </el-button>
      </div>
    </el-dialog>

    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="bankProductIncomeFormVisible"
      @close="resetForm('bankTransactionForm')"
    >
      <el-form :model="bankBill" :rules="bankTransactionRules" ref="bankTransactionForm">
        <el-form-item label=":" prop="name" label-width="100px">
          <el-input v-model="bankMyProduct.bank.name" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="bankName" label-width="100px">
          <el-input v-model="bankMyProduct.bank.bankName" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="bankCard" label-width="100px">
          <el-input v-model="bankMyProduct.bank.bankCard" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="transactionTime" label-width="100px">
          <el-date-picker
            v-model="bankBill.transactionTime"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label=":" v-if="income_show" prop="nextProfitDate" label-width="100px">
          <el-date-picker
            v-model="bankBill.nextProfitDate"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=":"
          prop="investmentAmount"
          v-if="investmentAmount_show"
          label-width="100px"
        >
          <el-input v-model="bankMyProduct.investmentAmount" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" prop="transactionAmount" label-width="100px">
          <font size="4" face="arial">{{this.Utils.transform(bankBill.transactionAmount) }}</font>
          <el-input v-model="bankBill.transactionAmount" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" label-width="100px">
          <el-input v-model="bankBill.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bankProductIncomeFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitBankTransaction('bankTransactionForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import searchs from "@/components/search/search";
export default {
  data() {
    return {
      total: 0, //
      currentPage: 1, //
      pageSize: 10,
      bankMyProducts: [],
      userNames: [],
      bankNames: [],
      param: { userName: "", bankName: "", bankCard: "" },
      name: "",
      bankIn: "",
      banks: [],
      income_show: false,
      investmentAmount_show: false,
      bankMyProduct: {
        bank: {
          id: "",
          name: "",
          bankName: "",
          bankCard: ""
        },
        productType: "",
        bankProduct: "",
        interestPaymentMethod: "",
        expectedInterestIncomeMonth: "",
        expectedInterestIncomeTotal: "",
        expectedinterestRate: "",
        state: 0
      },
      buyMyProduct: {
        bankCardId: "",
        productType: "",
        bankProduct: "",
        interestPaymentMethod: "",
        expectedInterestIncomeMonth: "",
        expectedInterestIncomeTotal: "",
        expectedinterestRate: "",
        state: 0
      },
      bankBill: {
        bankCardId: "",
        myProductId: "",
        transactionTime: "",
        transactionType: "",
        transactionAmount: "",
        transferCard: "",
        remark: ""
      },
      param: { name: "", bank: "", bankCard: "", times: "" },
      bankProductBackup: Object.assign({}, this.bankProduct),
      multipleSelection: [],
      buyBankProductFormVisible: false,
      bankProductIncomeFormVisible: false,
      dialogTitle: "",
      rowIndex: 9999,
      buyMyProductRules: {
        bankCardId: [{ required: true, message: "", trigger: "blur" }],
        productType: [{ required: true, message: "", trigger: "blur" }],
        investmentAmount: [
          { required: true, message: "", trigger: "blur" }
        ],
        buyingTime: [{ required: true, message: "", trigger: "blur" }],
        dueTime: [{ required: true, message: "", trigger: "blur" }],
        interestStartTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        expectedInterestRate: [
          { required: true, message: "", trigger: "blur" }
        ],
        interestPaymentMethod: [
          { required: true, message: "", trigger: "blur" }
        ],
        profitDate: [{ required: true, message: "", trigger: "blur" }],
        expectedInterestIncomeMonth: [
          { required: true, message: "", trigger: "blur" }
        ],
        expectedInterestIncomeTotal: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      bankTransactionRules: {
        transactionTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        nextProfitDate: [
          { required: true, message: "", trigger: "blur" }
        ],
        transactionAmount: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getMyBankProducts();
    this.getBanks();
    this.initBanks();
  },
  components: {
    searchs
  },
  methods: {
    getMyBankProducts() {
      let postData = this.$qs.stringify({
        page: this.currentPage,
        rows: this.pageSize,
        name: this.name,
        bank: this.bank
      });
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/bankMyProducts/select",
        data: this.param
      })
        .then(res => {
          if (res.data.code == 0) {
            this.bankMyProducts = res.data.data;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    initBanks() {
      this.loading = true;
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/banks/selectAll"
      })
        .then(res => {
          if (res.data.code == 0) {
            this.banks = res.data.data;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    formatter(row, column) {
      return this.Utils.toMoney(row[column.property]) + "";
    },
    getBanks() {
      this.loading = true;
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/banks/selectUserNamesAndBankNames"
      })
        .then(res => {
          if (res.data.code == 0) {
            this.userNames = res.data.data.userNames;
            this.bankNames = res.data.data.bankNames;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    handleEdit(index, row) {
      this.$message({
        type: "success",
        message: ","
      });
    },
    reminderRule(index, row) {
      this.$message({
        type: "success",
        message: ","
      });
    },
    redeem(index, row) {
      this.bankMyProduct = Object.assign({}, row);
      this.bankProductIncomeFormVisible = true;
      this.rowIndex = index;
      this.dialogTitle = "";
      this.investmentAmount_show = true;
      this.bankBill.myProductId = row.id;
      this.bankBill.bankCardId = this.bankMyProduct.bank.id;
      this.bankBill.transactionType = 9;

      // this.$confirm(` ${row.productType} ?`, "", {
      //   confirmButtonText: "",
      //   cancelButtonText: "",
      //   type: "warning"
      // })
      //   .then(() => {
      //     this.$message({
      //       type: "success",
      //       message: "!"
      //     });
      //   })
      //   .catch(() => {
      //     console.log("");
      //   });
    },
    income(index, row) {
      this.bankMyProduct = Object.assign({}, row);
      this.bankProductIncomeFormVisible = true;
      this.rowIndex = index;
      this.income_show = true;
      this.dialogTitle = "";
      this.bankBill.myProductId = row.id;
      this.bankBill.bankCardId = this.bankMyProduct.bank.id;
      this.bankBill.transactionType = 6;
    },
    submitBankTransaction(bankTransactionForm) {
      // 
      this.$refs[bankTransactionForm].validate(valid => {
        if (valid) {
          this.$http({
            method: "post",
            url: this.BASE_API + "/api/bankMyProducts/income",
            data: this.bankBill
          })
            .then(res => {
              if (res.data.code == 0) {
                this.$message({
                  type: "success",
                  message: ""
                });
                this.bankProductIncomeFormVisible = false;
                this.income_show = false;
                this.investmentAmount_show = false;
                 this.getMyBankProducts();
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg
                });
              }
            })
            .catch(err => {
              console.error(err);
            });
        }
      });
    },
    submitMyBankProduct(buyMyProductForm) {
      // 
      this.$refs[buyMyProductForm].validate(valid => {
        if (valid) {
          this.$http({
            method: "post",
            url: this.BASE_API + "/api/bankMyProducts/buy",
            data: this.buyMyProduct
          })
            .then(res => {
              if (res.data.code == 0) {
                this.$message({
                  type: "success",
                  message: ""
                });
                this.buyBankProductFormVisible = false;
                this.getMyBankProducts();
                this.resetForm(buyMyProductForm);
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg
                });
              }
            })
            .catch(err => {
              console.error(err);
            });
        }
      });
    },
    selectChange(val) {
      this.multipleSelection = val;
    },
    handleAdd() {
      this.dialogTitle = "";
      this.buyBankProductFormVisible = true;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="scss" scoped>
.bankProduct-box {
  width: 100%;
  .tool-box {
    padding: 10px 10px;
    border-bottom: 1px solid #eee;
  }
  .el-pagination {
    margin-top: 20px;
  }
}
</style>



