<template>
  <div class="bankLog-box">
    <searchs @search="getBankLogs" />
    <el-table :data="bankLogs" @selection-change="selectChange" style="width: 100%">
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="bankName" label=""></el-table-column>
      <el-table-column prop="bankCard" label=""></el-table-column>
      <el-table-column prop="realTransactionAmount" :formatter="formatter" label=""></el-table-column>
      <el-table-column prop="transactionTypeMsg" label=""></el-table-column>
      <el-table-column prop="transactionParty" label=""></el-table-column>
      <el-table-column prop="transactionCard" label=""></el-table-column>
      <el-table-column prop="transactionTime" label="" width="180"></el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
    </el-table>
    <el-pagination
      background
      :page-sizes="[10, 20, 30, 50]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="400"
    ></el-pagination>
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="bankLogFormVisible"
      @close="resetForm('bankLogForm')"
    >
      <el-form :model="bankLog" :rules="rules" ref="bankLogForm">
        <el-form-item label="" prop="name" label-width="50px">
          <el-input v-model="bankLog.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-input v-model="bankLog.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-input v-model="bankLog.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-date-picker
            v-model="bankLog.date"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bankLogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitBankLog('bankLogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
 
<script>
import searchs from "@/components/search/search";
export default {
  data() {
    return {
      bankLogs: [],
      bankLog: {
        id: "",
        date: "",
        name: "",
        phone: "",
        address: "",
        status: 0
      },
      bankLogBackup: Object.assign({}, this.bankLog),
      multipleSelection: [],
      bankLogFormVisible: false,
      dialogTitle: "",
      rowIndex: 9999,
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 5, message: " 2  5 ", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getBankLogs();
  },
  components: {
    searchs
  },
  methods: {
    getBankLogs(param) {
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/bankBill/query",
        data: param ? param : {}
      })
        .then(res => {
          if (res.data.code == 0) {
            this.bankLogs = res.data.data;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    handleEdit(index, row) {
      this.dialogTitle = "";
      this.bankLog = Object.assign({}, row);
      this.bankLogFormVisible = true;
      this.rowIndex = index;
    },
    submitBankLog(formName) {
      // 
      this.$refs[formName].validate(valid => {
        if (valid) {
          let id = this.bankLog.id;
          if (id) {
            // id-
            this.bankLogs.splice(this.rowIndex, 1, this.bankLog);
          } else {
            // id-
            this.bankLog.id = this.bankLogs.length + 1;
            this.bankLogs.unshift(this.bankLog);
          }
          this.bankLogFormVisible = false;
          this.$message({
            type: "success",
            message: id ? "" : ""
          });
        }
      });
    },
    handleDelete(index, row) {
      this.$confirm(` ${row.name} ?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.bankLogs.splice(index, 1);
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          console.log("");
        });
    },
    resetForm(formName) {
      this.$refs[formName].clearValidate();
    },
    formatter(row, column) {
      return this.Utils.toMoney(row[column.property]) + "";
    },
    mulDelete() {
      let len = this.multipleSelection.length;
      if (len === 0) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        this.$confirm(` ${len} `, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: `${len}`
            });
          })
          .catch(() => {
            console.log("");
          });
      }
    },
    selectChange(val) {
      this.multipleSelection = val;
    },
    handleAdd() {
      this.dialogTitle = "";
      this.bankLog = Object.assign({}, this.bankLogBackup);
      this.bankLogFormVisible = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.bankLog-box {
  width: 100%;
  .tool-box {
    padding: 10px 10px;
    border-bottom: 1px solid #eee;
  }
  .el-pagination {
    margin-top: 20px;
  }
}
</style>



