export default class Utils {
  // 
  static todayDate() {
    let dt, y, m, d, w
    dt = new Date()
    y = dt.getFullYear()
    m = dt.getMonth() + 1
    d = dt.getDate()
    w = dt.getDay()
    let weeks = ['', '', '', '', '', '', '']
    return `${y}  ${m.toString().padStart(2, '0')}  ${d.toString().padStart(2, '0')}  ${weeks[w]}`
  }

  static getTime() {
    let date = new Date()
    let yy = date.getFullYear()
    let mm = date.getMonth() + 1
    let dd = date.getDate()
    let hh = date.getHours()
    let mf = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
    let ss = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
    return yy + '-' + mm + '-' + dd + ' ' + hh + ':' + mf + ':' + ss
  }
  // 
  static fullScreen() {
    let isFull = document.isFullScreen || document.mozIsFullScreen || document.webkitIsFullScreen

    // 
    if (isFull) {
      let close = document.exitFullscreen || document.webkitCancelFullScreen || document.mozCancelFullScreen || document.msExitFullscreen
      close && close.call(document)
    } else {
      let docElm = document.documentElement
      let open = docElm.requestFullScreen || docElm.webkitRequestFullScreen || docElm.mozRequestFullScreen || docElm.msRequestFullscreen
      open && open.call(docElm)
    }
  }

  static toMoney(num) {
    if (num) {
      if (isNaN(num)) {
        alert('');
        return;
      }
      num = typeof num == 'string' ? parseFloat(num) : num // 
      num = num.toFixed(2); // 
      //console.log(num)
      num = parseFloat(num); // 
      num = num.toLocaleString(); // 
      // 
      if (num.indexOf('.') === -1) {
        num = '' + num + '.00';
      } else {
        console.log(num.split('.')[1].length)
        // num = num.split('.')[1].length < 2 ? '' + num + '0' : '' + num;
      }
      return num; // 23,245.122
    } else {
      return 0;
    }
  }

  // 
  static splits(tranvalue) {
    var value = new Array('', '');
    var temp = tranvalue.split(".");
    for (var i = 0; i < temp.length; i++) {
      value[i] = temp[i];
    }
    return value;
  }

  static transform(tranvalue) {
    // 
    if (tranvalue) {
      var i = 1;
      var dw2 = new Array("", "", "");// 
      var dw1 = new Array("", "", "");// 
      var dw = new Array("", "", "", "", "", "", "", "", "", "");// 
      //      
      // 
      var source = this.splits(tranvalue);
      var num = source[0];
      var dig = source[1];

      // 
      var k1 = 0;// 
      var k2 = 0;// 
      var sum = 0;
      var str = "";
      var len = source[0].length;// 
      for (i = 1; i <= len; i++) {
        var n = source[0].charAt(len - i);// 
        var bn = 0;
        if (len - i - 1 >= 0) {
          bn = source[0].charAt(len - i - 1);// 
        }
        sum = sum + Number(n);
        if (sum != 0) {
          str = dw[Number(n)].concat(str);// str
          if (n == '0') sum = 0;
        }
        if (len - i - 1 >= 0) {// 
          if (k1 != 3) {// 
            if (bn != 0) {
              str = dw1[k1].concat(str);
            }
            k1++;
          } else {// 
            k1 = 0;
            var temp = str.charAt(0);
            if (temp == "" || temp == "")// 
              str = str.substr(1, str.length - 1);
            str = dw2[k2].concat(str);
            sum = 0;
          }
        }
        if (k1 == 3)// 
        { k2++; }
      }

      //
      var strdig = "";
      if (dig != "") {
        var n = dig.charAt(0);
        if (n != 0) {
          strdig += dw[Number(n)] + "";//
        }
        var n = dig.charAt(1);
        if (n != 0) {
          strdig += dw[Number(n)] + "";//
        }
      }
      str += "" + strdig;
      return str;
    }

  }




}



